html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #102654;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 45px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  border: 2px solid #ffffff;
  outline: 2px solid #ffffff;
  user-select: none;
  transition: opacity 0.3s ease;
}
.button:hover {
  opacity: 0.6;
}
.button:focus-visible {
  outline-offset: 2px;
}
.button:active {
  opacity: 0.3;
}

.page-header {
  padding-top: 30px;
  padding-bottom: 51px;
  background-image: conic-gradient(from 133deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}
@media (max-width: 1199px) {
  .page-header {
    padding-top: 31px;
    padding-bottom: 82px;
    background-image: conic-gradient(from 144deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}
@media (max-width: 767px) {
  .page-header {
    padding-top: 11px;
    padding-bottom: 81px;
    background-image: conic-gradient(from 167.26deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}

.page-header__container {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 50px;
}

.page-header__logo-link {
  margin-top: 10px;
  transition: opacity 0.3s ease;
}
.page-header__logo-link:hover {
  opacity: 0.6;
}
.page-header__logo-link:focus-visible {
  outline: 2px solid #ffffff;
  outline-offset: 4px;
}
.page-header__logo-link:active {
  opacity: 0.3;
}

.page-header__logo {
  --desktopFooterLogo: none;
  --tabletLogo: none;
  --mobileLogo: none;
  display: block;
  width: 119px;
  height: 23px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .page-header__logo {
    --desktopHeaderLogo: none;
    --tabletLogo: block;
    width: 81px;
    height: 16px;
  }
}
@media (max-width: 767px) {
  .page-header__logo {
    --tabletLogo: none;
    --mobileLogo: block;
    width: 67px;
    height: 13px;
  }
}

.page-header__phone-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 20px;
}
@media (max-width: 1199px) {
  .page-header__phone-wrapper {
    margin-top: 2px;
  }
}
@media (max-width: 767px) {
  .page-header__phone-wrapper {
    margin-top: 5px;
  }
}

.page-header__phone {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  transition: opacity 0.3s ease;
}
@media (max-width: 1199px) {
  .page-header__phone {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}
@media (max-width: 767px) {
  .page-header__phone {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}
.page-header__phone:hover {
  opacity: 0.6;
}
.page-header__phone:focus-visible {
  outline: 2px solid #ffffff;
  outline-offset: 4px;
}
.page-header__phone:active {
  opacity: 0.3;
}

.page-header__intro {
  margin-top: -12px;
}
@media (max-width: 1199px) {
  .page-header__intro {
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .page-header__intro {
    margin-top: 66px;
  }
}

.intro {
  display: grid;
  grid-template-areas: ". label image" "list title image" ". button image";
  grid-template-columns: 11.4% 25.3% 1fr;
  grid-template-rows: 1fr min-content min-content;
  gap: 17px;
}
@media (max-width: 1199px) {
  .intro {
    grid-template-columns: 16.6% 28.2% 1fr;
    gap: 5px 25px;
  }
}
@media (max-width: 767px) {
  .intro {
    grid-template-areas: ". label" "list title" "button button";
    grid-template-columns: 38.8% 1fr;
    gap: 15px;
  }
}

.intro__label {
  grid-area: label;
  align-self: self-end;
  margin: 0;
  margin-left: 17px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  color: #ffffff;
  text-align: center;
}
@media (max-width: 1199px) {
  .intro__label {
    margin-right: 9px;
    margin-bottom: 3px;
    font-size: 34px;
    line-height: 41px;
    text-align: right;
  }
}
@media (max-width: 767px) {
  .intro__label {
    margin: 0;
    margin-right: 3px;
    font-size: 28px;
    line-height: 34px;
  }
}

.intro__title {
  grid-area: title;
  margin: 0 47px 32px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-align: right;
  text-transform: uppercase;
  -webkit-text-stroke: 3px;
}
@media (max-width: 1199px) {
  .intro__title {
    margin-right: 4px;
    margin-bottom: 32px;
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
  }
}
@media (max-width: 767px) {
  .intro__title {
    margin-right: 0;
    margin-bottom: 9px;
  }
}
@media (max-width: 599px) {
  .intro__title {
    margin-left: -40px;
  }
}

.intro__list {
  grid-area: list;
  align-self: self-end;
  margin: 0;
  margin-bottom: 68px;
  padding: 0;
  list-style-type: none;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  text-align: right;
}
@media (max-width: 1199px) {
  .intro__list {
    margin-bottom: 52px;
  }
}
@media (max-width: 767px) {
  .intro__list {
    margin-top: 70px;
    margin-bottom: 41px;
  }
}

.intro__button {
  grid-area: button;
  justify-self: center;
  min-width: 230px;
  margin-right: 32px;
  margin-bottom: 44px;
  padding: 21px 20px 20px;
}
@media (max-width: 1199px) {
  .intro__button {
    justify-self: self-end;
    min-width: 183px;
    margin-right: 5px;
    margin-bottom: 24px;
  }
}
@media (max-width: 767px) {
  .intro__button {
    width: calc(100% - 2px);
    margin-right: 2px;
    margin-bottom: 0;
  }
}

.intro__image-wrapper {
  grid-area: image;
}
@media (max-width: 767px) {
  .intro__image-wrapper {
    display: none;
  }
}

.intro img {
  width: 500px;
  height: 556px;
  object-fit: contain;
}
@media (max-width: 1199px) {
  .intro img {
    width: 324px;
    height: 390px;
  }
}

